.nav--primary > ul > li {
  position: relative;
  display: inline-block;
  vertical-align: top; }
  .nav--primary > ul > li > a {
    display: block;
    padding: var(--padding-3) var(--padding-6);
    position: relative; }
    .nav--primary > ul > li > a:not(.btn):hover {
